import React from 'react';
import ReactDOM from 'react-dom';

import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";

class Home extends React.Component {
    render(){
      return(
        <div className="container-fluid main-section">
            <div className="">    
                <div className="row">
                    <div className="col-lg-4 col-md-3 col-12">
                        <div className="main-heading-text">
                            <h1>Instagram Story Template</h1>
                        </div>
                    </div>
                    <div className="col-lg-8 col-md-9 col-12">
                        <div className="row insta-boxs">
                            <Link to="/Insta_detail" className="img-box frist">
                                <div className="img-container">
                                    <div className="img-text">
                                        <h3>Instragram Story</h3>
                                    </div>              
                                </div>
                            </Link>
                            <div className="img-box second">
                                <div className="img-text">
                                    <h3>Instragram Post</h3>
                                </div>
                            </div>
                            <div className="img-box last">
                                <div className="img-text">
                                    <h3>Instragram Highlight</h3>                  
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
      )
    }
  }
  export default Home; 